import { Injectable } from '@angular/core';
import { ArticleEntry } from "../../classes/ArticleEntry";
import { HttpClient } from "@angular/common/http";
import { catchError, tap } from 'rxjs/operators';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

@Injectable()
export class CartService {

    private cart: ArticleEntry[] = [];
    public sum: number;
    private URL = '';

    constructor(private httpClient: HttpClient) {
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {
            console.log(`${operation} failed: ${error.message}`);
            return of(result as T);
        };
    }

    getCart(): ArticleEntry[] {
        return this.cart;

    }

    add(article: ArticleEntry) {
        this.cart = [];
        this.cart.push(article);
        this.saveCart();
        this.updateSum();


    }

    updateSum(): Observable<number> {
        const url = `${this.URL}/getSum`;
        return this.httpClient.get<number>(url)
            .pipe(
                tap((sum) => {
                    console.log("Sum is:", sum);
                    return sum;
                }),
                catchError(this.handleError<number>(`User nicht gefunden`))
            );
    }

    update() {
        this.httpClient.get('/getCart').toPromise().then(res => {
            this.cart = res["cartItems"];
        }).catch(err => {
            console.error(err);
        });
    }

    delete(article: ArticleEntry) {
        this.cart.forEach((item, index) => {
            if (item.id == article.id) {
                this.cart.splice(index, 1);
            }
        });
        this.deleteItem(article);
    }

    deleteItem(article: ArticleEntry) {
        this.httpClient.post('deleteItem', {
            "item": article
        }).toPromise().then(res => {
            console.log(res)
        }).catch(err => {
            console.error(err)
        });
    }


    saveCart() {
        this.httpClient.post('/setCart', {
            "cart": this.cart

        }).toPromise().then(res => {
            console.log(res);
        });
    }
}



