import { Injectable } from '@angular/core';
import { Message } from '../../classes/message';
import { UserService } from '../user/user.service';
import * as io from 'socket.io-client';

@Injectable()
export class ChatService {
    public username: string = '';
    public messages: Message[] = [
        new Message('Öffentlich', 'Hier werden deine Nachrichten von allen Nutzern gesehen.' +
            ' Oder klicke auf einen Namen, um dieser Person eine Privatnachricht zu senden. ', 'all', true)
    ];
    public userID: string = '';
    public privateMessageName: string = 'all';
    public pm: boolean = false;
    public showChat: boolean = false;
    public socket;
    public message: string;
    public clientList = [];
    public firstOpen = true;
    public counter: number;
    public unreadMessages: boolean = false;

    constructor(public userService: UserService) {
    }

    sendMessage() {
        if (this.privateMessageName != 'all') {
            this.messages.push(new Message(this.username, this.message, this.privateMessageName, this.pm));
        }
        let data = new Message(this.username, this.message, this.privateMessageName, this.pm);
        this.socket.emit('chat', data);
        this.privateMessageName = 'all';
        this.pm = false;
        this.message = null;
    }

    setPmName(data) {
        if (this.privateMessageName == 'all') {
            this.privateMessageName = data;
        } else {
            this.privateMessageName = 'all';
        }
        this.pm = (this.pm ? this.pm = false : this.pm = true);
        this.showChat = true;
    }

    toggleChat() {
        if (this.firstOpen) {
            this.firstOpen = false;
            this.userService.showProfileData()
                .subscribe((data) => {
                        this.username = data.displayName;
                        this.socket = io.connect(window.location.protocol + '//' + window.location.host);
                        this.socket.emit('socket_info', {userID: this.userID, username: this.username});
                        this.socket.on('getClientList', (data) => {
                            this.clientList.splice(0, this.clientList.length);
                            for (let i = 0; i < data.length; i++) {
                                this.clientList.push(data[i].username);
                            }
                            console.log(this.clientList);
                        });
                        this.socket.on('chat', (data) => {
                            if (!this.showChat) {
                                this.counter += 1;
                                this.unreadMessages = true;
                            }
                            this.messages.push(data);
                            console.log(this.messages);

                        });
                    },
                    (err) => {
                        console.log("Error getProfileData()", err);
                    });
        }
        this.showChat = (this.showChat ? this.showChat = false : this.showChat = true);
        this.unreadMessages = false;
        this.counter = 0;
    }

}



