import { Component, Input, OnInit } from '@angular/core';
import { User } from '../../classes/User';
import { UserService } from '../../services/user/user.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { UserEditComponent } from '../user-edit/user-edit.component';

@Component({
    selector: 'app-user',
    templateUrl: './user.component.html',
    styleUrls: ['./user.component.scss']
})


export class UserComponent implements OnInit {
    public firstName: string;
    public lastName: string;
    public email: string;
    public street: string;
    public number: number;
    public city: string;
    public zip: number;
    public id: string;
    public userId: string;
    public username: string;
    public key: string;

    public user: User;

    public fullProfileInfo : boolean = false;
    thankYouAlert = false;



    constructor(public userservice: UserService, private modalService: NgbModal) { }

    ngOnInit() {
        this.getProfileData()
    }

    getUser(): void {
        this.userservice.getUser(this.userId)
            .subscribe((data) => {
                console.log("getUser()", data);
                console.log(this.userId);
                for (let i = 0; i < data.length; i++) {
                    if (data[i].userId == this.userId) {
                        this.user = data[i];
                        this.email = data[i].email.valueOf();
                        this.username = data[i].displayName;
                        this.firstName = data[i].firstName;
                        this.lastName = data[i].lastName;
                        this.userId = data[i].userId;
                        this.street = data[i].street;
                        this.number = data[i].number;
                        this.city = data[i].city;
                        this.zip = data[i].zip;
                        this.key = data[i].key;
                    }
                }
                if (data.length != 0) {
                    this.fullProfileInfo = true;
                }
            });
    }

    getProfileData(): void {
        this.userservice.showProfileData()
            .subscribe((data) => {
                    this.user = data;
                    // console.log("getProfileData from login",data);
                    this.email = data.email[0]['value'];
                    this.username = data.displayName;
                    this.firstName = data.firstName;
                    this.lastName = data.lastName;
                    this.userId = data.userId;
                    this.getUser();

                },
                (err) => {
                    console.log("Error getProfileData()", err);
                });
    }

    add(): void {
        if (!this.street && !this.number && !this.city && !this.zip) {
            return;
        }
        let newUser: User = new User(
            this.firstName,
            this.lastName,
            this.email,
            this.street,
            this.number,
            this.city,
            this.zip,
            this.userId);
        console.log("New user: ", newUser);
        this.userservice.addUser(newUser)
            .subscribe((result) => {
                    console.log("component:", result);
                    this.getUser();
                },
                (err) => {
                    console.log("Error add().user.component.ts", err)
                });
        this.fullProfileInfo = true;
        this.thankYouAlert = true;

    }

    editUser() {
        this.getUser();
        const modalReference = this.modalService.open(UserEditComponent);
        modalReference.componentInstance.user = this.user;
        console.log(modalReference.componentInstance);
        console.log("editUser().openModal.key:", modalReference.componentInstance.user.key);
        modalReference.result.then((modalUser) => {
            console.log("edited user:", modalUser);
            this.userservice.editUser(modalUser);
        }).catch((error) => {
            console.error("User could not be updated: " + error);
        })
    }

    deleteUser(): void {
        this.getUser();
        console.log(this.user);
        this.firstName = "";
        this.lastName = "";
        this.email = "";
        this.street = "";
        this.number = null;
        this.city = "";
        this.zip = null;
        this.userId = "";
        this.key = "";
        this.userservice.deleteUser(this.user);
    }

}



