import { Injectable } from '@angular/core';
import { ArticleEntry } from '../../classes/ArticleEntry';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';

@Injectable()
export class ShopService {
    private articles: ArticleEntry[];

    constructor(private http: HttpClient) {
        this.getAllArticles();
    }

    private getAllArticles() {
        this.http.get('/allArticles').toPromise()
            .then((data: any) => {
                this.articles = data.allArticles;
            }).catch((err) => ShopService.handleError(err));
    }

    showArticles(): ArticleEntry[] {
        return this.articles;
    }

    private static handleError(error: HttpErrorResponse): void {
        console.error(`Fehler: ${error.message}`);
    }
}



