export class User {
    firstName: string;
    lastName: string;
    displayName: string;
    username: string;
    userId: string;
    email: string;
    street: string;
    number: number;
    city: string;
    zip: number;
    key: string;


    constructor(firstName?: string, lastName?: string, email?: string, street?: string,
                number?: number, city?: string, zip?: number, userId?: string, key?: string) {
        this.firstName = firstName;
        this.lastName = lastName;
        this.email = email;
        this.street = street;
        this.number = number;
        this.city = city;
        this.zip = zip;
        this.userId = userId;
        this.key = key;
    }
}



