import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { User } from '../../classes/User';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, tap, map } from 'rxjs/operators';
import { Router } from '@angular/router';

const httpOptions = {
    // headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable()
export class UserService {

    public firstName: string;
    public lastName: string;
    public username: string;
    public password: string;
    public id: string;
    public email: string;
    public street: string;
    public number: number;
    public city: string;
    public zip: number;
    public key: string;

    private userURL = '';  // starting URL dirceting to user

    constructor(private http: HttpClient, private router: Router) { }

    public user: User;

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {
            console.log(`${operation} failed: ${error.message}`); // transforming error for better user consumption
            return of(result as T); // Let the app keep running by returning an empty result.
        };
    }

    public getUser(userId: string): Observable<User[]> {
        console.log("übergebene userId", userId);
        const url = `${this.userURL}/user/${userId}`;
        return this.http.get<User[]>(url)
            .pipe(
                tap((result) => {
                    console.log("Users from Database are:", result);
                    return result;
                }),
                catchError(this.handleError<User[]>(`User nicht gefunden`))
            );
    }

    public addUser(user: User): Observable<User> {
        console.log("User in Service:", user);
        const url = `${this.userURL}/user`;
        return this.http.post<User>(url, user, httpOptions)
            .pipe(
                tap((newUser: User) => {
                    console.log("New user in Database: ", newUser);
                    return newUser;
                })
            );
    }


    public editUser(userToEdit: User) {
        console.log("editUser().key:", userToEdit.key);
        const url = 'https://localhost:8443/userUpdate';
        return this.http.put(url, userToEdit, httpOptions)
            .subscribe(() => {
                    console.log("User successfully updated.");
                }, (err) => {
                    console.log('Error:', err);

                }
            );
    }

    public deleteUser(user: User) {
        const url = 'https://localhost:8443/userDelete';
        return this.http.post(url, user)
            .subscribe((data) => {
                    console.log("Deleting user worked.");
                    this.router.navigateByUrl("/login");
                }, (err) => {
                    console.log('Error:', err);
                }
            );

    }

    public showProfileData(): Observable<User> {
        const url = `${this.userURL}/userProfile`;
        return this.http.get<User>(url).pipe(
            tap((user) => {
                console.log("Datenabfrage showProfileData() worked");
            }),
            catchError(this.handleError<User>('Userdaten konnten nicht geladen werden.'))
        )
    }

}



