import { Component, OnInit } from '@angular/core';
import { ShopService } from '../../services/shop/shop.service';
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { ShowArticleComponent } from "../show-article/show-article.component";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { ArticleEntry } from "../../classes/ArticleEntry";
import { CartService } from "../../services/cart/cart.service";

@Component({
    selector: 'app-shop',
    templateUrl: './shop.component.html',
    styleUrls: ['./shop.component.scss']
})
export class ShopComponent {


    constructor(public shopService: ShopService, public cartService: CartService, private router: Router,
                private route: ActivatedRoute, private modalService: NgbModal) {
    }

    showArticle(article: ArticleEntry) {
        const modalReference = this.modalService.open(ShowArticleComponent);
        modalReference.componentInstance.article = article;
        console.log(modalReference.componentInstance);
        modalReference.result.then((result: any) => {
            this.cartService.add(result);
        }).catch((error) => {
            console.error("windows closed: " + error);
        })
    }

}



