export class Message {
    username: string;
    message: string;
    sendTo: string;
    pm: boolean;
    date: string;

    constructor(username: string, message: string, sendTo: string, pm: boolean) {
        this.username = username;
        this.message = message;
        this.sendTo = sendTo;
        this.pm = pm;
        let options = {
            day: 'numeric',
            month: 'short',
            hour: '2-digit',
            minute: '2-digit'
        };
        this.date = new Date().toLocaleString('de-de', options);

    }
}



